<template>
  <div class="content">
    <!--     下拉框-->
    <div class="Select" >
      <p @click="selectClick">{{selectName}}<span :class="'vantIcon '+selectIcon"><van-icon name="arrow-down" /></span></p>
    </div>
    <div class="selectList" v-if="selectShow">
      <ul  >
        <li v-for="(item,index) in selectList" :class="index==itemId?'actives':''"  @click="liClick(item,index)" >{{item.name}}</li>
      </ul>
    </div>
    <!--   统计表-->
    <div class="statis  statisTop">
      <div class="statiStical">
        <h2><span></span><p>订单数据</p></h2>
        <ul>
          <li>
            <div>
              <span>支付订单数</span>
              <p>40</p>
            </div>
          </li>
          <li>
            <span>取车订单数</span>
            <p>4</p>
          </li>
          <li>
            <span>退款订单数</span>
            <p>40</p>
          </li>
        </ul>
      </div>
      <div class="marginTop">
        <!--        <eCharts></eCharts>-->
        <div id="eCharId1" class="eChars"></div>
        <div class="marginTop">
          <timeList  @timeChang="timeChang"></timeList>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  const timeList = () => import("../../../components/timeList3.vue");
  export default {
    components:{
      timeList,
    },
    data() {
      return {
        day:this.common.timeForMat(7,true)+" - "+this.common.timeForMat(0,true),
        selectShow:false,
        selectIcon:false,
        selectName:"门店名称",
        itemId:"-2",
        selectList:[
          {name:"XXXXX门店名称1XXXXX门店名称1"},
          {name:"XXXXX门店名称1XXXXX门店名称2"},
          {name:"XXXXX门店名称1XXXXX门店名称3"},
          {name:"XXXXX门店名称1XXXXX门店名称4"},
          {name:"XXXXX门店名称1XXXXX门店名称5"},
          {name:"XXXXX门店名称1XXXXX门店名称6"},
          {name:"XXXXX门店名称1XXXXX门店名称"},
          {name:"XXXXX门店名称1XXXXX门店名称"},
          {name:"XXXXX门店名称1XXXXX门店名称"},
          {name:"XXXXX门店名称1XXXXX门店名称"},
          {name:"XXXXX门店名称1XXXXX门店名称"},
          {name:"XXXXX门店名称1XXXXX门店名称"},
          {name:"XXXXX门店名称1XXXXX门店名称"},
          {name:"XXXXX门店名称1XXXXX门店名称"},
          {name:"XXXXX门店名称1XXXXX门店名称"},
          {name:"XXXXX门店名称1XXXXX门店名称"},
          {name:"XXXXX门店名称1XXXXX门店名称"},
          {name:"XXXXX门店名称1XXXXX门店名称555"},
          {name:"XXXXX门店名称1XXXXX门店名称444"},
          {name:"XXXXX门店名称1XXXXX门店名称333"},
          {name:"XXXXX门店名称1XXXXX门店名称222"},
        ]
      }
    },
    mounted(){
      this.common.eChart2("eCharId1");
    },
    methods:{
      timeChang(data){
        console.log(data)
        if(data.length>10){
          this.day=this.common.transForm(data);
        }else {
          this.day=this.common.transForm(data)+" - "+this.common.timeForMat(0,true);
        }
      },
      selectClick(){
        if(this.selectShow){
          this.selectIcon="iconbto";
          this.selectShow=false;
        }else{
          this.selectIcon="iconTop";
          this.selectShow=true;
        }
      },
      liClick(data,index){
        this.selectClick();
        this.selectName=data.name;
        this.itemId=index;
      }
    },
  }
</script>
<style scoped lang="scss">
  .eChars{
    width: 100%;
    height: 532px;
  }
  .marginTop{
    margin-top:20px;
  }
  .statis{
    background: #fff;
    padding-bottom: 42px;

  }
  .statisTop{
    margin-top:100px;
  }
  .WithdrawalData{
    border-top: 20px solid #F8F8F8;
    padding-bottom: 150px;
  }
  .statiStical{
    padding-top: 20px;
  }
  .statiStical h2{
    height: 60px;
    padding: 10px  36px;
  }
  .statiStical  h2 p{
    text-align: left;
    float: left;
    height: 40px;
    line-height: 40px;
    font-size:28px;
    font-family:PingFangSC;
    font-weight:500;
    color:rgba(51,51,51,1);
    margin-left: 20px;
  }
  .statiStical  h2 span{
    height: 28px;
    width: 10px;
    background: #FF8D59;
    float: left;
    margin-top: 6px;
  }
  .statiStical ul{
    margin-top: 20px;
    padding: 20px;
    box-sizing: border-box;
    display: flex;
  }
  .statiStical ul li{
    flex: 1;
    padding-left: 30px;
    box-sizing: border-box;
    border-left: 1px solid #E6E8EE;
  }
  .statiStical ul li:nth-child(1){
    border: none;
  }
  .statiStical ul li div{
    margin: 0 auto;
    display: inline-block;
  }
  .statiStical ul li span{
    font-size:24px;
    font-family:PingFangSC;
    font-weight:400;
    color:rgba(153,153,153,1);
    line-height:34px;
    text-align: left;
    display: block;
  }
  .statiStical ul li p{
    text-align: left;
    font-size:48px;
    font-family:PingFangSC;
    font-weight:600;
    color:rgba(51,51,51,1);
    line-height:76px;
  }

  .content{
    min-height: 100%;
  }
  .selectList{
    font-family:PingFangSC;
    font-weight:500;
    color:rgba(51,51,51,1);
    position: fixed;
    left: 0;
    top: 182px;
    background:rgba(0,0,0,0.5);
    height: 1140px;
    /*overflow: hidden;*/
    overflow: scroll;
    padding-bottom: 100px;
    width: 100%;
    z-index: 99999999;
    ul{
      padding: 0  40px;
      background: #ffffff;
      width: 100%;
      box-sizing: border-box;
      padding-bottom: 100px;
      li{
        padding:32px 0;
        width: 100%;
        box-sizing: border-box;
        font-size:26px;
        font-family:PingFangSC;
        font-weight:400;
        color:rgba(72,72,72,1);
        line-height:36px;
        text-align: left;
      }
      .actives{
        color: #FF8D59;
      }
    }
  }
  .Select{
    width: 100%;
    line-height: 96px;
    font-size:32px;
    font-family:PingFangSC;
    font-weight:500;
    color:rgba(51,51,51,1);
    position: fixed;
    z-index: 999999999;
    left: 0;
    top: 86px;
    border-top:2px solid #F0F0F0 ;
    box-shadow:0px 2px 2px 0px  rgba(0,0,0,0.08);
    p{
      background: #ffffff;
      width: 100%;
      height:96px;
      text-align: left;
      padding: 0 100px 0 36px;
      box-sizing: border-box;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      overflow: hidden;
    }
    .vantIcon{
      position: absolute;
      right: 36px;
      top: 4px;
      i{
        font-size: 32px;
        color: #333333;
        font-weight:600;
      }
    }
    .vantIcon.iconTop{
      transform: rotate(180deg) ;
      transition: transform 0.3s;
    }
    .vantIcon.iconbto{
      transform: rotate(0deg) ;
      transition: transform 0.3s;
    }
  }
</style>



